import React from 'react'
import {NewTask, NewTaskTags} from './components'

class Form extends React.Component {

	constructor(){
		super()

	}

	render(){

		return(
			<form>
				<NewTask />
				<NewTaskTags handleAddTag={this.addTag} />
			</form>
		)
	}

}

export default Form



